.footer {
  padding-bottom: 2rem;
}
.footer-header {
  text-align: center;
  margin-bottom: 2rem;
}
.social-icons {
  display: flex;
  justify-content: space-around;
  font-size: 1.5rem;
}

.social-icons a:visited {
  color: #000;
}

.social-icons a:hover {
  color: #574694;
}

@media screen and (min-width: 768px) {
  .footer {
    max-width: 100%;
    margin: 0 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem;
  }
  .footer-header {
    padding: 0;
    margin: 0;
    text-align: unset;
  }
  .social-icons {
    display: flex;
  }
  .social-icons a {
    margin: 10px;
    font-size: 30px;
    transition: var(--transition);
  }
}
