.projects {
  margin: 2rem;
  animation: fadein 0.5s;
}
.section-title {
  margin-bottom: 2rem;
}
.blog-cards {
  display: grid;
  column-gap: 2rem;
  row-gap: 3rem;
}
.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.blog-img {
  height: 300px;
  width: 100%;
  object-fit: cover;
  margin-bottom: 20px;
  border-radius: 10px;
}
.card-link {
  width: fit-content;
  color: #ffffff !important;
  font-size: 1.25rem;
  padding: 0.4rem 1.1rem;
  background: #3d1f38;
  border-radius: 15px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.card-link:hover {
  background-color: #574694;
}
.card-paragraph {
  font-size: 1.25rem;
}
@media screen and (min-width: 768px) {
  .projects {
    margin: 2rem 4rem;
    padding: 1rem;
  }
  .blog-cards {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    column-gap: 6rem;
    row-gap: 3rem;
  }
}
