nav {
  padding: 0.5rem;
  margin: 0 2rem;
}
.nav-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav-toggle {
  font-size: 1.5rem;
  background: transparent;
  border-color: transparent;
  transition: var(--transition);
  cursor: pointer;
}
.nav-toggle:hover {
  color: #574694;
}

.logo {
  font-size: 1.75rem;
}
.name {
  margin: 0.75rem;
  font-size: 1.5rem;
  text-transform: uppercase;
}
.links a {
  font-size: 1.25rem;
  text-transform: capitalize;
  display: block;
  margin: 1rem 0;
  width: fit-content;
}
.links a:hover {
  color: #574694;
  transition: var(--transition);
}

.links-container {
  height: 0;
  overflow: hidden;
  transition: var(--transition);
  background-color: #9b739a;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  left: 0;
  right: 0;
}
.show-container {
  height: 10rem;
}

@media screen and (min-width: 768px) {
  nav {
    max-width: 100%;
    margin: 0 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem;
  }
  .nav-toggle {
    display: none;
  }
  .links-container {
    height: auto !important;
    width: unset;
    position: unset;
  }
  .links {
    display: flex;
  }
  .links a {
    margin: 1rem;
  }
  .links a:last-child {
    margin-right: 0;
  }
}
